.mobile-form-container form {
  padding-top: 20px;
  text-align: center;
}

.mobile-form-container {
  max-width: 500px;
  margin: auto;
}

input {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  border: 1px solid #888;
  box-shadow: 0px 2px 4px #ccc;
  padding: 10px;
  border-radius: 6px;
  margin-top: 20px;
  font-size: 18px;
  width: 90%;
  max-width: 400px;
}

textarea {
  background: rgba(255, 255, 255, 0.5);
  resize: none;
  font-family: "Roboto", sans-serif;
  width: 90%;
  max-width: 400px;
  border: 1px solid #888;
  border-radius: 6px;
  box-shadow: 0px 2px 4px #ccc;
  padding: 10px;
  margin-top: 40px;
  font-size: 18px;
}


.form-container {
  text-align: center;
  width: 60%;
  max-width: 500px;
  margin: auto;
}

.form-container p {
  font-size: 20px;
}

.form-container form {
  padding-top: 20px;
}

.submit-success-popup,
.submit-error-popup {
  pointer-events: none;
  opacity: 0;
  position: fixed;
  top: 10px;
  z-index: 5000;
  width: 100%;
}

.popup-card {
  margin: auto;
}

.popup-card p {
  display: inline-block;
  position: relative;
  margin-left: 5px;
  transform: translateY(-10px);
  font-size: 16px;
  text-align: center;
}

.popup-content {
  width: 300px;
  margin: auto;
  padding: 5px 10px;
  background: white;
  border-radius: 6px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, .4);
}

.popup-image {
  width: 35px;
  padding: 10px;
  height: 35px;
  display: inline-block;
}

.submit-success-popup {
  z-index: 5000;
}

.submit-error-popup {
  z-index: 4900;
}

@media (min-width: 800px) {
  .submit-success-popup,
  .submit-error-popup {
    top: 5px;
    width: 100%;
  }

  .popup-card p {
    margin-left: 20px;
    transform: translateY(-15px);
    font-size: 20px;
  }

  .popup-content {
    width: 380px;
    padding: 10px 20px;
  }

  .popup-image {
    width: 50px;
    padding: 10px;
    height: 50px;
    display: inline-block;
  }
}

.popup-visible {
  transition: 0s;
  opacity: 1;
}

.mobile-only {
  display: block;
  width: 100%;
}

.desktop-only {
  display: none;
}

@media (min-width: 800px) {
  .mobile-only {
    display: none;
  }

  .desktop-only {
    display: block;
  }
}

