/* Navbar Logo Styling */
.logo-container {
    transition: 300ms;
    height: 100px;
    width: 100%;
    padding: 20px 0px 20px;
    background: #fafafa;
    z-index: 1000;
}

.logo-container h4 a {
    text-decoration: none;
}

.logo-container h4 {
    color: #2c426f;
    font-family: "Times New Roman", serif;
    font-weight: 900;
    font-style: italic;
    font-size: 8px;
    margin: 10px auto 20px;
    text-align: center;
    opacity: 1;
    transition: 300ms opacity;
}

@media (min-width: 360px) {
    .logo-container h4 {
        font-size: 10px;
    }
}

.logo {
    transition: 300ms;
    opacity: 1;
    width: 50%;
    max-width: 250px;
    margin: auto;
}

.scrolled .logo-container {
    height: 0px;
    padding: 0px;
    transition: 300ms;
}

.scrolled .logo-container .logo {
    transition: 300ms;
    width: 0px;
    opacity: 0;
}

.scrolled .logo-container h4 {
    transition: 200ms;
    opacity: 0;
}

.logo img {
    width: 100%;
}

.logo img:hover {
    opacity: 0.8;
}

@media (min-width: 600px) {
    .logo-container h4 {
        transition: 300ms;
        font-size: 14px;
    }
}

@media (min-width: 1100px) {
    .logo-container h4 {
        margin-bottom: 0px;
    }
}
