.hero-container {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 700px;
}


.hero-container:after {
  content: " ";
  z-index: 10;
  display: block;
  position: relative;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  pointer-events: none;
}

.title {
    position: absolute;
    text-align: center;
    transform: translateY(320px);
    text-shadow: 0px 2px 4px #000000;
    width: 100%;
    z-index: 20;
}

.title h1 {
    color: white;
    letter-spacing: 3px;
    padding: 30px;
    font-size: 2px;
}

.hsq-title h1 {
    font-size: 1px;
}

.title h4 {
  color: white;
  margin: 0;
  font-style: italic;
  font-size: 20px;
  letter-spacing: 2px;
}

.hero-btn-container {
    z-index: 1000;
    position: absolute;
    transform: translateY(180px);
    width: 100%;
}

.hero-btn {
    text-align: center;
}

.hero-btn a {
    transition: 200ms;
    background: rgba(0, 0, 0, 0.6);
    color: white;
    text-align: center;
    border: 3px solid white;
    border-radius: 6px;
    padding: 20px 60px;
    font-size: 20px;
    text-decoration: none;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
}

.hero-btn a:hover {
    transition: 200ms;
    background: rgba(0, 0, 0, 0.8);
    box-shadow: 0px 0px 0px 2px white, 0px 8px 15px rgba(0, 0, 0, 0.3);
}

@media (min-width: 600px) {
    .hero-btn-container {
        transform: translateY(240px);
    }
}

@media (min-width: 800px) {
    .hero-btn a {
        padding: 20px 80px;
    }

    .hero-btn-container {
        position: absolute;
        transform: translateY(550px);
        width: 100%;
    }
}

@media (min-width: 1300px) {
    .hero-btn-container {
        transform: translateY(560px);
    }
}

@media (min-width: 1600px) {
    .hero-btn-container {
        transform: translateY(640px);
    }

    .hero-btn a {
        font-size: 24px;
    }
}

@media (min-width: 800px) {
    .title h1 {
        font-size: 56px;
    }

    .hsq-title h1 {
        font-size: 32px;
    }
}

@media (min-width: 900px) {
    .title h1 {
        font-size: 64px;
    }

    .hsq-title h1 {
        font-size: 36px;
    }
}

@media (min-width: 1000px) {
    .title h4 {
        font-size: 24px;
    }

    .hsq-title h1 {
        font-size: 42px;
    }
}

@media (min-width: 1100px) {

    .hsq-title h1 {
        font-size: 48px;
    }
}

@media (min-width: 1300px) {
    .title h4 {
        font-size: 28px;
    }

    .hsq-title h1 {
        font-size: 56px;
    }
}

@media (min-width: 1600px) {
    .title {
        transform: translateY(360px);
    }
    .title h1 {
        font-size: 72px;
    }

    .hsq-title h1 {
        font-size: 60px;
    }

    .title h4 {
        font-size: 32px;
    }

    .hero-container {
        height: 800px;
    }
}