.desktop-nav {
    display: none;
}

.mobile-nav {
    display: block;
}

.position-mobile-nav {
    position: absolute;
    width: 100%;
    z-index: 2000;
}

.navbar-container {
    width: 100%;
    background: #3c485e;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
}

.mobile-nav-button {
    height: 30px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    transition: 300ms;
}

.nav-item span {
    font-size: 12px;
    margin-left: 30px;
}

@media (min-width: 360px) {
    .nav-item span {
        font-size: 16px;
    }
}


.nav-item {
    padding: 15px 0px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    transition: 300ms;
}

.nav-items-container a {
    text-decoration: none;
    color: white;
}

.mobile-nav-button:hover,
.nav-item:hover {
    cursor: pointer;
    background: #28313f;
}

.svg {
    text-align: center;
}

.svg svg {
    margin-top: 2px;
}



@media (min-width: 1100px) {
    .desktop-nav {
        display: block;
        background: white;
    }

    .mobile-nav {
        display: none;
    }

    /* Navbar Styling */
    .navbar-container {
        position: relative;
        background: #95a0a9;
        text-align: center;
        z-index: 1000;
    }

    .nav-items-container {
        display: inline;
        margin: auto;
        padding: 15px 0px;
        background: white;
        border-left: 2px solid white;
        border-right: 2px solid white;
    }

    .nav-item {
        box-shadow: none;
        display: inline-block;
        text-align: center;
        padding: 15px 0px;
    }

    .mobile-nav-button:hover,
    .nav-item:hover {
        cursor: pointer;
        background: none;
    }

    .nav-item a {
        position: relative;
        background: #dadee2;
        color: #333;
        text-decoration: none;
        padding: 15px 20px;
        transition: 200ms ease-in-out;
        margin: 0px 2px;
    }

    .nav-item a:hover {
        padding: 20px;
        background: #2c426f;
        color: white;
        box-shadow: 0px 2px 4px #000000aa;
    }

    .active a {
        background: #2c426f;
        color: white;
    }

    .active a:hover {
        padding: 15px 20px;
        box-shadow: none;
        cursor: default;
    }


}